/* eslint-disable no-restricted-globals */
// src/scripts/sw.js

self.addEventListener('install', (event) => {
  event.waitUntil(
    caches.open('restaurant-apps-cache').then((cache) => cache.addAll([
      '/', // add the root URL
      '/index.html',
      '/detail.html',
      '/favourite.html',
      '/styles/main.css',
      '/styles/detail.css',
      '/styles/favourite.css',
      '/scripts/index.js',
      '/scripts/detail.js',
      '/scripts/favourite.js',
      '/scripts/sw.js',
    ])),
  );
});

self.addEventListener('fetch', (event) => {
  event.respondWith(
    caches.match(event.request).then((response) => response || fetch(event.request)),
  );
});
